import {
	getUserAddressList
} from '@/api/userApi.js'
export default {
	data() {
		return {
			list: [

			],
			from: '',
			hasOrders: true,
			platform: 0
		};
	},
	onLoad(options) {
		this.from = options.from || ''
		
	},
	onShow() {
		// 设备样式兼容
		this.platform = uni.getStorageSync('platform');
		// 获取用户地址列表
		this.getUserAddresList()
	},
	methods: {
		// 返回订单页面
		goBlack(item) {
			if (this.from === 'submit') {
				uni.setStorageSync('address', JSON.stringify(item))
				uni.navigateBack({
					delta: 1
				})
			}
		},
		// 获取用户地址列表
		getUserAddresList() {
			getUserAddressList().then(res => {
				if (res.code === '1000') {
					this.list = []
					this.list = res.data;
					this.hasOrders = this.list.length > 0;
				}else{
					this.hasOrders = this.orders.length > 0
				}
			})
		},
		goAddedit(id) {
			let url = id != 0 ? '/pages/user/addedit/addedit?id=' + id : '/pages/user/addedit/addedit'
			uni.navigateTo({
				url
			})
		}
	}
}
